import _get from 'lodash.get';
import { formatDate } from '../../../helpers/date';
import { TOffer } from '../../../types';

export const CONTINGENCY_STATUS = {
	done: 'Done',
	inProcess: 'In Process',
	overdue: 'OVERDUE',
};

export const getContingencyStatus = (offer: TOffer, name: string) => {
	const dueDate = _get(offer, ['Contingencies', `${name}DueDate`]);
	let dueDateFormatted;
	let status;

	if (dueDate) {
		dueDateFormatted = formatDate(dueDate);
		status =
			formatDate(new Date().toDateString()) <= dueDateFormatted
				? CONTINGENCY_STATUS.inProcess
				: CONTINGENCY_STATUS.overdue;
	}

	const isRemoved = _get(offer, ['Contingencies', `${name}Removed`]);
	if (isRemoved === 'Yes') {
		status = CONTINGENCY_STATUS.done;
	}

	return {
		status,
		dueDate: dueDateFormatted,
	};
};
