import {
	Table,
	TableBody,
	TableContainer,
	Typography,
	TableRow,
	TableCell,
	TableHead,
} from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';
import { TOffer } from '../../../../types';
import SubCard from '../../../../ui-component/cards/SubCard';
import { getContingencyStatus } from '../helper';
import { CONTINGENCY_STEP } from '../config';

type Props = {
	offer: TOffer;
};

type TContingency = {
	name: string;
	title: string;
	dueDate: string | undefined;
	status: string | undefined;
};

export const mappingOfferContingencies = (
	offer: TOffer
): Array<TContingency> => [
	{
		name: 'FinanceContingency',
		title: 'Finance Contingency Removal',
		...getContingencyStatus(offer, 'FinanceContingency' || ''),
	},
	{
		name: 'InspectionContingency',
		title: 'Inspection Contingency Removal',
		...getContingencyStatus(offer, 'InspectionContingency' || ''),
	},
	{
		name: 'SaleOfHomeContingency',
		title: 'Homesale Contingency Removal',
		...getContingencyStatus(offer, 'SaleOfHomeContingency' || ''),
	},
];

const Contingency = (props: Props) => {
	const { offer } = props;
	const contingencies = mappingOfferContingencies(offer);
	const isAllRemoved = _get(contingencies, 'AllRemoved', false);
	const fullAddress = _get(offer, ['PropertyData', 'FullAddress']);
	const stepId = _get(offer, 'StepId', 0);
	const isHidden = stepId < CONTINGENCY_STEP;

	if (_isEmpty(contingencies) || isAllRemoved || isHidden) return <></>;

	return (
		<SubCard title='Contingencies Status'>
			<Typography variant='subtitle2'>{fullAddress}</Typography>
			<TableContainer>
				<Table
					sx={{
						'& td': {
							borderBottom: 'none',
						},
					}}
					size='small'
				>
					<TableHead>
						<TableRow>
							<TableCell>Type</TableCell>
							<TableCell>Due Date</TableCell>
							<TableCell>Status</TableCell>
						</TableRow>
					</TableHead>
					<TableBody>
						{contingencies.map((contingency: TContingency) => (
							<TableRow key={contingency.title}>
								<TableCell variant='head'>{contingency.title}</TableCell>
								<TableCell variant='head'>{contingency.dueDate || 'n/a'}</TableCell>
								<TableCell variant='head'>{contingency.status || 'n/a'}</TableCell>
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
		</SubCard>
	);
};

export default Contingency;
