/* eslint-disable camelcase */
import { Button, makeStyles } from '@material-ui/core';
import _get from 'lodash.get';
import _isEmpty from 'lodash.isempty';

type TSubmissionFile = {
	Files: [
		{
			presignedUrl: string;
		}
	];
	QuestionName: string;
};

type Props = {
	submissionFiles: TSubmissionFile[];
};

const PRE_APPROVAL = 'fd_upload_buyer_preapproval_letter';
const PROOF_OF_FUNDS = 'fd_upload_buyer_proof_of_funds';
const FUND_PROOF = 'fundingtype_fund_proof';

const useStyles = makeStyles(theme => ({
	downloadLink: {
		marginRight: theme.spacing(2),
	},
}));
const navigateToExternalUrl = (
	url: string = '',
	shouldOpenNewTab: boolean = true
) =>
	shouldOpenNewTab ? window.open(url, '_blank') : (window.location.href = url);

const DownloadButton = ({ submissionFiles }: Props) => {
	const classes = useStyles();
	const renderButton = (
		source: TSubmissionFile[],
		type: string,
		label: string
	) => {
		const files = source.find((file: TSubmissionFile) => {
			const questionName = _get(file, ['QuestionName'], '');
			return questionName === type;
		});
		const preSignedUrl = _get(files, ['Files', '0', 'presignedUrl']);
		if (_isEmpty(preSignedUrl)) return <></>;
		return (
			<Button
				variant='outlined'
				color='secondary'
				className={classes.downloadLink}
				type='button'
				onClick={() => navigateToExternalUrl(preSignedUrl)}
			>
				{label}
			</Button>
		);
	};

	if (_isEmpty(submissionFiles)) return <></>;

	return (
		<>
			{renderButton(submissionFiles, PRE_APPROVAL, 'Pre-Approval Letter')}
			{renderButton(submissionFiles, PROOF_OF_FUNDS, 'Proof of Funds')}
			{renderButton(submissionFiles, FUND_PROOF, 'Proof of Funds')}
		</>
	);
};
export default DownloadButton;
